html {
    --b: #000;
    --w: #fff;
    --g: #f8f8f8;
    --l: #898989;
    --hr: #cfcfcf;
}

.exercise {
    max-width: 556px;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.exercise form {
    display: flex;
    gap: 8px;
    width: calc(100% + 1.5px);
}

.exercise input, .exercise select {
    padding: 4px 8px;
    width: 100%;

    border: 1px solid var(--l);
    border-radius: 2px;

    font-family: 'Open Sans';
    font-size: 13px;
}

.exercise select {
    background: var(--g);
}

/* TABLE */
.exercise .border {
    width: 100%;
    border: 1px solid var(--l);
    border-radius: 3px;
    overflow: hidden;
}

.exercise table {
    width: 100%;
    border-collapse: collapse;
    font-size: 13px;
}

.exercise th {
    font-weight: 700;
    font-stretch: condensed;
    text-transform: uppercase;
    text-align: left;
}

.exercise table tbody tr {
    border-top: 1px solid var(--l);
}

.exercise table tbody tr:nth-child(odd) {
    background: var(--g);
}

.exercise table tr > * {
    padding: 4px 8px;
}
