* {
    padding: 0;
    margin: 0;
    transition: 0.1s;
    font-family: 'Open Sans', sans-serif;
}

body {
    padding: 80px 80px 210px;
    margin: auto;
    max-width: 1000px;
    display: flex;
    flex-direction: column;
    gap: 60px;
}

hr {
    border: 1px dashed #d0d0d0;
}

#heroes {
    display: flex;
    gap: 20px;
}

ul {
    padding-left: 13px;
    font-size: 14px;
    margin-top: -12px;
}

li {
    padding: 2px 0;
}

.class {
    color: #e5e7e6;
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 10px;
    border: thin solid #e5e6e7;
    padding: 20px;
    border-radius: 8px;
    width: 200px;
}

@media (max-width: 1100px){
    .class {
        width: 100%;
    }
}

.class:hover {
    color: initial;
    transform: scale(105%);
}

.hero {
    font-weight: 100;
    font-size: 12px;
    margin-bottom: 5px;
}

.table-table-border {
    display: flex;
}

.table-border {
    border-radius: 4px;
    border: 1px solid #898989;
}

.objectTable {
    width: 400px;
    font-size: 11px;
    border-collapse: collapse;
}

.objectTable td, .objectTable th {
    padding: 4px 14px;
}

.objectTable tr:nth-child(odd) td {
    background: #f7f7f7;
}

.objectTable tr:first-child td, .objectTable tr:first-child th {
    padding-top: 5px;
}

.objectTable tr:last-child td, .objectTable tr:last-child th {
    padding-bottom: 5px;
}

.objectTable tr:last-child td:first-child {
    border-bottom-left-radius: 4px;
}

.objectTable tr:last-child td:last-child {
    border-bottom-right-radius: 4px;
}

.objectTable tbody tr {
    border-top: 1px solid #898989;
}

.objectTable th {
    text-transform: uppercase;
    font-size: 11px;
    text-align: left;
}

#form, #hooks {
    width: 400px;
    display: flex;
    flex-direction: column;
    gap: 6px;
}

input, button, select {
    font-size: 11px;
    padding: 4px 6px;
    border-radius: 4px;
    border: 1px solid #898989;
}

#form .full-name {
    display: flex;
    gap: 6px;
    justify-content: space-between;
}

#form .full-name input {
    width: 50%;
}

#form .birth {
    display: flex;
    gap: 6px;
    justify-content: space-between;
}

#form .birth input {
    width: 100%;
}

#form button {
    font-family: 'JetBrains Mono';
    font-weight: 700;
}

select {
    background: #fff;
}

.useState {
    font-family: 'montserrat';
    display: flex;
    flex-direction: column;
    width: 100px;
    border: 1px solid #cacaca;
    padding: 20px 10px;
    gap: 4px;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    text-align: center;
}

.useState h1 {
    line-height: 20px;
}

button {
    font-family: 'Open Sans';
    letter-spacing: 1px;
    text-transform: uppercase;
    font-weight: 900;
    font-stretch: condensed;
    padding: 4px 6px 4px 7px;
    border: 1px solid #898989;
    border-radius: 2px;
}

.power {
    display: flex;
    font-size: 14px;
    align-items: center;
}

.power input {
    font-family: 'Open Sans', sans-serif;
    font-size: 14px;
    border: 1px solid #e9e9e9;
    padding: 2px 4px;
    width: 24px;
    text-align: right;
}

.power span {
    font-size: 10px;
    font-weight: 700;
    padding: 0 4px;
    align-self: flex-start;
}

#cycle h1 {
    text-align: left;
    color: #ea0042;
}

.title-p {
    font-style: italic;
    color: #5f5f5f;
    font-weight: 100;
    margin-bottom: 10px;
    font-size: 18px;
}

.seperated-style {
    padding: 0 3px;
    background: #00acf0;
    color: #fff;
    border-radius: 3px;
}

.sum {
    display: flex;
    gap: 10px;
    font-size: 12px;
    align-items: center;
}

.sum input {
    width: 20px;
    font-size: 12px;
    font-family: 'Open Sans', sans-serif;
    text-align: right;
}

#blog {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.blogs {
    display: flex;
    /* flex-direction: column; */
    gap: 20px;
}

.blog {
    display: flex;
    flex-direction: column;
    /* justify-content: space-between; */
    border: 1px solid #cacaca;
    border-radius: 4px;
    padding: 38px 40px 42px;
    gap: 15px;
    width: 100%;
    align-self: stretch;
}

.blog.clicked {
    border-color: #000;
    position: relative;
    width: 300%;
}

.blog h1 {
    text-transform: uppercase;
    line-height: 28px;
}

.blog .author {
    font-style: italic;
    font-size: 18px;
}

.blog .body {
    display: none;
    font-weight: 100;
    text-align: justify;
    height: 100px;
    overflow-y: auto;
    padding-right: 10px;
    scrollbar-width: 4px;
    scrollbar-width: thin;
}

@media (max-width: 1100px){
    .blog {
        width: initial;
    }

    .blogs {
        position: relative;
        width: 100%;
        flex-direction: column;
    }

    .blog.clicked {
        width: initial;
    }
}

.blog .body.clicked {
    display: block;
    font-weight: 100;
    text-align: justify;
}

#down {
    z-index: 1;
    font-family: 'JetBrains Mono';
    color: #fff;
    background: #000;
    position: fixed;
    bottom: 55px;
    right: 55px;
    outline: 3px solid #fff;
    border-radius: 4px;
    padding: 14.8px 6px;
    font-weight: 900;
    text-decoration: none;
    transition: 0.05s ease-in;
}

#down:hover {
    /* transform: scale(105%); */
    background: #ff0040;
    color: #fff;
}

#down:active {
    transform: translateY(1px);
    background: #00e9dd;
}

.article {
    display: flex;
    flex-direction: column;
    gap: 6px;
}
