html {
    --b: #000;
    --w: #fff;
    --g: #f8f8f8;
    --l: #898989;
    --hr: #cfcfcf;
}

.axios {
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 10px;
}

.axios .nou {
    font-stretch: condensed;
    text-transform: uppercase;
    font-size: 40px;
}

.axios .nou span {
    font-weight: 700;
}

.axios .cards {
    display: grid;
    grid-template-columns: repeat(3, 33.33%);
    /* width: 100%; */
    gap: 10px;
}

@media (max-width: 1000px) {
    .axios .cards {
    grid-template-columns: repeat(2, 50%);
    }
}

@media (max-width: 700px) {
    .axios .cards {
    grid-template-columns: 100%;
    }
}

.axios a {
    color: var(--b);
}

/* TABLE */
.axios .card {
    display: flex;
    flex-direction: column;
    padding: 20px;
    gap: 8px;
    /* width: 50%; */
    border: 1px solid var(--hr);
    background: var(--w);
    border-radius: 3px;
    overflow: hidden;
    align-items: flex-start;
    font-size: 13px;
}

.axios .card:hover {
    border: 1px solid var(--l);
}

.axios .card p {
    /* visibility: hidden; */
    opacity: 0.2;
}

.axios .card:hover * {
    /* visibility: visible; */
    opacity: 1;
}

.axios .card h1 {
    text-transform: uppercase;
    font-stretch: condensed;
    font-size: 30px;
    margin-bottom: -3px;
    margin-left: -1px;
}

.axios .card h2 {
    text-transform: uppercase;
    font-stretch: condensed;
    font-size: 20px;
    width: 27px;
    background: var(--b);
    color: var(--w);
    border-radius: 2px;
    text-align: center;
}

.axios table {
    width: 100%;
    border-collapse: collapse;
    font-size: 13px;
}

.axios th {
    font-weight: 700;
    font-stretch: condensed;
    text-transform: uppercase;
    text-align: left;
}

.axios table tbody tr {
    border-top: 1px solid var(--l);
}

.axios table tbody tr:nth-child(odd) {
    background: var(--g);
}

.axios table tr > * {
    padding: 4px;
}

.axios table tr > :first-child {
    padding-left: 12px;
}

.axios table tr > :last-child {
    padding-right: 12px;
}
